<template>
  <div id="user">
    <mt-header fixed title="用户中心"></mt-header>
    <my-login v-if="!isLogin" @getLogSta="getLogSta"></my-login>
    <my-userdis v-if="isLogin"></my-userdis>
    <div class="blank">
      <van-button class="logout" style="border-radius:8px" type="danger" size="large" @click="logout" v-if="isLogin">注销</van-button>
    </div>
  </div>
</template>

<script>
import myLogin from "../components/Login";
import myUserdis from "../components/Userdis";

export default {
  data() {
    return {
      isLogin: false,
      uname: ""
    };
  },
  components: {
    myLogin,
    myUserdis
  },
  mounted() {
    this.loadPage();
  },
  methods: {
    getLogSta(state, uname) {
      this.isLogin = state;
      this.uname = uname;
      // console.log(state,uname);
      window.localStorage.setItem("isLogin", state);
      if (uname) {
        window.localStorage.setItem("uname", uname);
      }
    },
    loadPage() {
      const status = localStorage.getItem("isLogin");
      const uname = localStorage.getItem("uname");
      // console.log(status,uname)
      if (status != null && uname != null) {
        this.isLogin = status;
        this.uname = uname;
      }
    },
    logout() {
      this.$messagebox({
        title: "提示",
        message: "注销当前账号",
        showCancelButton: true
      }).then(res => {
        if (res === "confirm") {
          window.localStorage.removeItem("uname");
          window.localStorage.setItem("isLogin", false);
          this.isLogin = false;
        }
      });
    }
  }
};
</script>
<style scoped>
.blank {
  padding: 0 5px;
  margin-top: 25px;
}
</style>