<template>
  <div id="useDis">
    <div class="userInfo">
      <!-- <div class="title">当前用户</div> -->
      <div class="unameBox">
        <div class="uname">{{uname}}</div>
      </div>
    </div>
    <div class="tool">
      <mt-cell class="toolItem" title="我的收藏" value="查看"></mt-cell>
      <mt-cell class="toolItem" title="生日">
        <span v-if="birth" @click="checkBirth">{{birth}}</span>
      </mt-cell>
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        :min-date="minDate"
        :max-date="maxDate"
        item-height="30"
        visible-item-count="5"
        @confirm="confirmBirth"
        @cancel="cancel"
        class="checkBirth"
        style="display:none"
      />
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      uname: "",
      status: "",
      birth: "选择",
      minDate: new Date(1949, 0, 1),
      maxDate: new Date(2020, 12, 31),
      currentDate: new Date()
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      const uname = window.localStorage.getItem("uname");
      const isLogin = window.localStorage.getItem("isLogin");
      const birth = window.localStorage.getItem("birth");
      this.uname = uname;
      this.status = isLogin ? "已登录" : "未登录";
      this.birth = birth ? birth : "选择";
    },
    confirmBirth(value) {
      // console.log(value);
      let year = value.getFullYear();
      let month = value.getMonth() + 1;
      this.birth = `${year}-${month}`;
      window.localStorage.setItem("birth", this.birth);
      // console.log(this.birth);
      const chec = document.getElementsByClassName("checkBirth")[0];
      chec.style.display = "none";
    },
    cancel() {
      const chec = document.getElementsByClassName("checkBirth")[0];
      chec.style.display = "none";
    },
    checkBirth() {
      const chec = document.getElementsByClassName("checkBirth")[0];
      chec.style.display = "block";
    }
  }
};
</script>
<style scoped>
#useDis {
  padding: 0 5px;
}

.userInfo {
  margin-top: 30%;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
}

.title {
  height: 40px;
  line-height: 40px;
  border-radius: 8px;
  vertical-align: middle;
  background-color: cornflowerblue;
  color: #fff;
}

.unameBox {
  width: 80px;
  height: 80px;
  margin: 15px auto;
  border: 4px solid burlywood;
  border-radius: 50%;
}

.uname {
  width: 70px;
  height: 70px;
  line-height: 70px;
  border: 5px solid cadetblue;
  border-radius: 50%;
  font-weight: bolder;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.tool {
  margin-top: 15%;
}

.toolItem {
  border-bottom: 1px solid #ccc;
}
</style>