<template>
  <div class="login">
    <h2>用户登录</h2>
    <!-- 添加用户名/密码框 -->
    <hr />
    <mt-field
      type="text"
      label="用户名"
      placeholder="请输入用户名"
      v-model="uname"
      :state="unstate"
      @blur.native.capture="checkUname"
    ></mt-field>
    <hr />
    <mt-field
      type="password"
      label="密码"
      placeholder="请输入密码"
      v-model="upwd"
      :state="upstate"
      @blur.native.capture="checkUpwd"
    ></mt-field>
    <hr />
    <!-- 添加登录按钮 -->
    <mt-button size="large" @click="login" type="primary">登录</mt-button>
    <router-link to="/register" class="register">
      <span class="iconfont icon-zhuce"></span>没有账号？现在注册
    </router-link>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uname: "",
      upwd: "",
      unstate: "",
      upstate: ""
    };
  },

  created() {},

  methods: {
    // 登录的时候只需要验证是否为空吧？
    checkUname() {
      // console.log(111);
      // 1.创建正则表达式 字母数字3~12位
      // var reg = /^[a-z0-9]{3,12}$/i;

      // 2.验证用户名如果格式不正确，提示格式不正确
      // if(!reg.test(this.uname)) {
      if (this.uname === "") {
        // this.$messagebox("消息","用户名格式不正确");
        this.unstate = "error";
        return;
      } else {
        this.unstate = "success";
      }
    },
    checkUpwd() {
      // 1.创建正则表达式 字母数字3~12位
      // var reg = /^[a-z0-9]{3,12}$/i;

      // 2.验证密码如果格式不正确，提示密码格式不正确
      // if(!reg.test(this.upwd)) {
      if (this.upwd === "") {
        // this.$messagebox("提示","密码格式不正确");
        this.upstate = "error";
        return;
      } else {
        this.upstate = "success";
      }
    },
    login() {
      // console.log(123);
      // 4.发送ajax请求
      // this.axios.get('https://api.apiopen.top/musicRankings').then(res => {
      //     console.log(1)
      //     console.log(res);
      // })
      if (this.unstate == "success" && this.upstate == "success") {
        // 1.获取用户名和密码
        var uname = this.uname;
        var upwd = this.upwd;

        // 2.创建url请求服务器程序地址
        var url = "/local/login";

        // 3.创建obj请求数据
        var dataObj = `uname=${uname}&upwd=${upwd}`;

        // console.log(1);
        this.axios.post(url, dataObj).then(res => {
          // console.log(res);
          // 5.获取服务器返回信息
          var result = res.data;
          // 6.如果服务器返回-1，提示
          // 7.如果返回1，跳转到/product列表组件
          if (result.code == -1) {
            this.$messagebox("提示", result.msg);
            this.$emit("getLogSta", false);
          } else if (result.code == 1) {
            this.$toast({
              message: "登录成功",
              position: "middle",
              duration: "2000",
              iconClass: "iconfont icon-chenggong"
            });
            this.$emit("getLogSta", true, uname);
            setTimeout(() => {
              // this.$router.push('/user');
            }, 2000);
          }
        });
      } else {
        this.$messagebox("提示", "用户名或密码格式不正确");
        this.$emit("getLogSta", false);
      }
    }
  }
};
</script>

<style scoped>
.login {
  margin-top: 25%;
  padding: 0 5px;
  text-align: center;
}

a {
  font-size: 14px;
  font-weight: bold;
  color: #c4c4c4;
  text-decoration: none;
  user-select: none;
}
.register {
  margin-top: 10px;
  float: right;
}
.register span {
  display: inline-block;
  margin-right: 3px;
}
</style>